.post {
  background-color: white;
  border: 1px solid lightgray;
  max-width: 600px;
  margin-bottom: 45px;
}
.post_header {
  display: flex;
  padding: 10px;
  align-items: center;
}
.post_image {
  width: 100%;
  object-fit: contain;
  border-top: 1px solid lightgray;
  border-bottom: 1px solid lightgray;
}
.post_caption {
  padding: 10px;
  font-weight: normal;
}
.post_avatar {
  margin-right: 10px;
}
.post_comment {
  margin: 1rem auto;
  padding: 0.5rem;
  display: flex;
}
.comment_inp {
  flex: 1;
  border: none;
}
.post_button {
  flex: 0;
}
