* {
  box-sizing: border-box;
  margin: 0;
}
.App {
  background-color: #fafafa;
  height: 100vh;
}
header {
  background-color: white;
  padding: 10px;
  width: 100%;
  height: 55px;
  border-bottom: 1px solid lightgray;
  display: flex;
  justify-content: space-between;
}
header img {
  object-fit: contain;
}
#logo {
  width: 100px;
  height: 40px;
}
.app_signup {
  display: flex;
  flex-direction: column;
}
.all_posts {
  padding: 1.2rem 4%;
  display: flex;
  justify-content: center;
}
